            console.log(response.data)
            cities.value = response.data
        })
})

const getCountries = (()=>{
    CountryDataService.getAll()
        .then((response)=>{
            countries.value = response.data
        })
})

const person_types = ref([])

const getPersonTypes = (()=>{
    TypePersonDataService.getAll()
        .then((response)=>{
            console.log(response.data)
            
            person_types.value = response.data
            person_types.value.forEach((element: any)  => {
                if(element.name === 'cliente')
                {
                    ClientTypePerson.value = element
                }

                if(element.name === "referencia")
                {
                    ReferenceTypePerson.value = element
                }
            });
        })
})

const savePerson = (()=>{
    // console.log(person.value)
    let payload = person.value
    payload.account_id = Account.value.id
    payload.identity_card_city_id =person.value.identity_card_city.id
    payload.person_type_id = ClientTypePerson.value.id
    payload.gender = person.value.gender.id
    console.log('payload',payload)
    PersonDataService.create(payload)
        .then((response)=>{
            console.log(response)
            router.push('/clients')
        })
})

onMounted(()=>{
    console.log("load component")
    getCities()
    getCountries()
    getPersonTypes()
})

